.wp-block-slidercesar-slider {
	margin: 0 auto;
}

.editor-styles-wrapper .wp-block-slidercesar-slider .wp-block {
	max-width: unset;
}
.slidercesar__slide {
	min-width: 100px;
	min-height: 100px;

	.slidercesar__button--remove {
		position: absolute;
		z-index: 99;
		top: 0;
		right: 0;
		border: 1px solid;
		margin: 5px;
		background-color: rgba(255, 255, 255, 0.25);
		display: none;
	}
	&.is-selected,
	&.is-hovered,
	&:hover {
		.slidercesar__button--remove {
			display: flex;
		}
	}
}
.slidercesar__window {
	width: 0;
	min-width: 100%;
	.slidercesar__slide_inner {
		top: 2%;
	}
}
.slidercesar__navigation {
	top: 50%;
	height: 0px;
}
.slidercesar__button__navigation {
	height: auto;
}

.slidercesar__buttons {
	align-items: center;
}
.slidercesar__button {
	display: flex;
	justify-content: center;
	text-align: center;
	button {
		line-height: 0;
		background-color: transparent;
		border: none;
		cursor: pointer;

		&:focus {
			outline-style: none;
		}
	}
	.rich-text {
		min-width: 20px;
		opacity: 0.5;
		transition: all 0.5s;
		line-height: 1em;
	}

	&--active .rich-text {
		opacity: 1;
	}
}

.editor-styles-wrapper .slidercesar__track {
	> .block-editor-inner-blocks {
		width: 100%;

		> .block-editor-block-list__layout {
			display: flex;
			width: 100%;
			height: 100%;
		}
	}

	.inner-blocks {
		overflow: auto;
	}
}
.editor-styles-wrapper div[data-type="slidercesar/slide"] {
	position: relative;
	display: flex;
	margin: 0;
	max-width: unset;
	width: 100%;

	.block-editor-inner-blocks {
		width: 100%;
	}
}

button.components-button.has-icon.has-text.button-full-width {
	width: 100%;
	justify-content: center;
}
/* fade slider */
.editor-styles-wrapper
	div[data-transition-effect="fade"]
	div[data-type="slidercesar/slide"] {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;

	opacity: 0;

	&.slidercesar__slide--active-editor {
		z-index: 1;
		opacity: 1;
	}
}
.components-base-control {
	width: 100%;
}
.components-panel__row {
	.slidercesar__button.is-secondary {
		margin-left: auto;
	}
}

.slidercesar__slide__bg > img {
	min-height: 100%;
}

.ssb-tabPanel .components-tab-panel__tabs-item {
	padding: 3px 10px;
}

.slidercesar__slide__image {
	display: none;
}
.extraLarge .visible--xl,
.desktop .visible--lg,
.tablet .visible--md,
.mobile .visible--sm {
	display: block;
}

.components-unit-control-wrapper {
	margin-bottom: 15px;
}
